#contact {
  height: fit-content;
  width: 100%;
  margin: auto;
  padding: 5rem;
  background: url("../../Assets/Background_texture/bg-texture.png");
  background-color: #000000;
  color: white;
}
.contact_heading {
  text-align: center;
  color: white;
}
.contact_info h2 {
  width: 50%;
  margin: 2rem auto;
  text-align: center;
}
.contact_options {
  width: 50%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  gap: 20px;
}
.contact_options a {
  text-decoration: none;
  color: white;
  cursor: pointer;
}
.contact_icons {
  color: var(--primary-color);
  font-size: 1.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
}
.contact_card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  transition: all 400ms ease-in-out;
  cursor: pointer;
}
.contact_card span{
    cursor: pointer;
}
.contact_card:hover {
  transform: scale(1.1);
}
#contact p {
  text-align: center;
  margin: 2rem;
  width: 100%;
}
/* ******************Responsive*********************** */
@media only screen and (max-width: 819px) {
  .contact_info h2 {
    width: 90%;
  }
  .contact_options {
    width: 100%;
    grid-template-columns: repeat(1, 1fr);
  }
  #contact p {
    margin: 0.5rem auto;
  }
}
@media only screen and (min-width: 768px) and (max-width: 820px) {
  .contact_options {
    width: 60%;
  }
}
/* Galaxy Fold */
@media only screen and (max-width: 280px) {
  .contact_info h2 {
    width: 100%;
  }
  .contact_options {
    width: 100%;
    gap: 5px;
  }
  #contact {
    padding: 1rem;
  }
  #contact p {
    text-align: center;
    margin: 0.5rem 0 3rem 0;
  }
}

